.container {
    min-width: 100%;
    display: grid;
    grid-template-columns: minmax(auto, 200) 1fr minmax(auto, 200);
    grid-template-rows: auto auto;
    grid-template-areas: 
    ". title ."
    ". container .";
}

.container h1:first-child {
    grid-area: title;
    border-bottom: 2px solid;
    font-size: 40px;
    margin-bottom: 20%;
}

.container :nth-child(n) {
    grid-area: container;
    text-align: center;
    font-size: 20px;
    border: none;
    font-family: 'Comfortaa';
    padding: 5px;
}

.login__btn {
    min-width: 100%;
    background-color: rgba(51, 51, 51, 0.5);
    border: none;
    color: #FFFFFF;
        text-align: center;
        font-size: 20px;
        font-family: 'Comfortaa';
        padding: 10px;
        transition: all 0.3s ease-out 0s;
        cursor: pointer;
        margin: 20px 2px;
        position: relative;
        right: 0;
}

.login__btn:hover{
    background-color: #333;
}

.login__btn:active{
    background-color: rgba(51, 51, 51, 0.5);
    transform: scale(0.9);
}

.input.error
{
    border: rgba(255, 0, 0, 0.788) 1px solid;
    color: rgb(31, 27, 26);
}

.message__error
{
    box-sizing: border-box;
    background-color: rgba(255, 0, 0, 0.2);
    margin-bottom: 20px;
}